body {
  margin: 0;
  font-family: "Raleway", "Verdana", "Arial", "Tahoma", sans-serif;
  font-size: 25px;
  font-weight: normal;
  line-height: 1.29;
  color: #E6E6E6; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Regular.ttf") format("ttf");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Medium.ttf") format("ttf");
  font-weight: 500; }

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Bold.ttf") format("ttf");
  font-weight: bold; }

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-ExtraBold.ttf") format("ttf");
  font-weight: 800; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }
